import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { MarkdownModule } from 'ngx-markdown';
import { HttpClientModule } from '@angular/common/http'

// import { MaterialModulesModule } from './shared/material-modules/material-modules.module'
import { AppComponent } from './app.component';
import { AppRoutingModule } from './shared/routing/app-routing.module';
import { YeelightAPIComponent } from './pages/yeelight-api/yeelight-api.component';
import { AboutComponent } from './pages/about/about.component'
import { NavigationComponent } from './shared/navigation/navigation.component';
import { HomeComponent } from './pages/home/home.component';
import { JiocComponent } from './pages/jioc/jioc.component';
import { Constants } from './shared/app-constants';
import { SubNavigationComponent } from './shared/sub-navigation/sub-navigation.component';

@NgModule({
  declarations: [
    AboutComponent,
    AppComponent,
    YeelightAPIComponent,
    AboutComponent,
    NavigationComponent,
    HomeComponent,
    JiocComponent,
    SubNavigationComponent
  ],
  imports: [
    HttpClientModule,
    MarkdownModule.forRoot({ loader: HttpClientModule }),
    BrowserModule,
    AppRoutingModule,
    // MaterialModulesModule
  ],
  // exports: [MaterialModulesModule],
  providers: [Constants],
  bootstrap: [AppComponent]
})
export class AppModule { }
