import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { YeelightAPIComponent } from '../../pages/yeelight-api/yeelight-api.component'
import { AboutComponent } from '../../pages/about/about.component'
import { HomeComponent } from '../../pages/home/home.component'
import { JiocComponent } from '../../pages/jioc/jioc.component'
import { SubNavigationComponent } from '../../shared/sub-navigation/sub-navigation.component'
import { Constants } from '../app-constants'

const routes: Routes = [
  { path: '', redirectTo: Constants.HOME_PATH, pathMatch: 'full' },
  { path:  Constants.HOME_PATH, component: HomeComponent },
  { path: Constants.YEELIGHTAPI_PATH , redirectTo: Constants.YEELIGHTAPI_HOME_PATH },
  { path: Constants.YEELIGHTAPI_HOME_PATH, component: YeelightAPIComponent },
  { path: Constants.YEELIGHTAPI_README_PATH, component: YeelightAPIComponent },
  { path: Constants.ABOUT_PATH, component: AboutComponent },
  { path: Constants.JIOC_PATH, redirectTo:Constants.JIOC_HOME_PATH },
  { path: Constants.JIOC_HOME_PATH, component: JiocComponent },
  { path: Constants.JIOC_README_PATH, component: JiocComponent },
];

@NgModule({
  exports: [RouterModule],
  imports: [RouterModule.forRoot(routes)]
})
export class AppRoutingModule {

}
